
// export default memo(ArchitectureDiagramRAG);
import React, { useState, useEffect, memo } from "react";
import "./ArchitectureDiagramRAG.css";

const ArchitectureDiagramRAG = () => {
  const [activeNode, setActiveNode] = useState(null);
  const [isAnimating, setIsAnimating] = useState(true);
  const [viewMode, setViewMode] = useState("full");
  
  // Demo State
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentStep, setCurrentStep] = useState(-1);
  const [demoQuery, setDemoQuery] = useState("What is machine learning?");

  // Demo Steps Data
  const demoSteps = [
    {
      nodeId: "user",
      title: "User Query",
      output: `"${demoQuery}"`,
      detail: "Natural language question submitted",
    },
    {
      nodeId: "frontend",
      title: "Frontend Processing",
      output: "Validated & sanitized input",
      detail: "React captures and sends to API",
    },
    {
      nodeId: "api",
      title: "API Gateway",
      output: "POST /api/search { query: '...' }",
      detail: "Request authenticated & routed",
    },
    {
      nodeId: "query_processor",
      title: "Query Processing",
      output: "Intent: DEFINITION | Entities: ['machine learning']",
      detail: "NLP analysis complete",
    },
    {
      nodeId: "embeddings",
      title: "Embedding Generation",
      output: "[0.234, -0.567, 0.891, 0.123, -0.456, ...]",
      detail: "1536-dim vector created (OpenAI Ada)",
    },
    {
      nodeId: "vector_db",
      title: "Vector Search",
      output: "Found 847 documents  Top 5 selected",
      detail: "Cosine similarity: 0.94, 0.91, 0.89...",
    },
    {
      nodeId: "retriever",
      title: "Document Retrieval",
      output: "Retrieved 3 chunks (2,847 tokens)",
      detail: "Sources: wiki.ml, arxiv.org, docs.ai",
    },
    {
      nodeId: "context_builder",
      title: "Context Assembly",
      output: "Prompt: [System] + [Context] + [Query]",
      detail: "4,096 tokens prepared for LLM",
    },
    {
      nodeId: "llm",
      title: "LLM Generation",
      output: `"Machine learning is a subset of AI that enables systems to learn from data..."`,
      detail: "GPT-4 response in 1.2s",
    },
  ];

  // Play Demo Logic
  useEffect(() => {
    let timer;
    if (isPlaying && currentStep < demoSteps.length - 1) {
      timer = setTimeout(() => {
        setCurrentStep((prev) => prev + 1);
      }, 2000);
    } else if (currentStep >= demoSteps.length - 1) {
      setTimeout(() => {
        setIsPlaying(false);
      }, 3000);
    }
    return () => clearTimeout(timer);
  }, [isPlaying, currentStep]);

  const startDemo = () => {
    setCurrentStep(0);
    setIsPlaying(true);
    setIsAnimating(false);
  };

  const resetDemo = () => {
    setCurrentStep(-1);
    setIsPlaying(false);
    setIsAnimating(true);
  };

  const arch = {
    nodes: [
      { id: "user", label: "User", icon: "", x: 8, y: 25, category: "main", description: "End user sends natural language queries" },
      { id: "frontend", label: "Frontend", icon: "", x: 22, y: 25, category: "main", description: "React/Next.js application" },
      { id: "api", label: "API Gateway", icon: "", x: 38, y: 25, category: "main", description: "FastAPI handling requests" },
      { id: "query_processor", label: "Query Processor", icon: "", x: 52, y: 25, category: "rag", description: "Analyzes user queries" },
      { id: "embeddings", label: "Embeddings", icon: "", x: 52, y: 50, category: "rag", description: "Text to vector conversion" },
      { id: "vector_db", label: "Vector Store", icon: "", x: 68, y: 50, category: "rag", description: "Semantic similarity search" },
      { id: "retriever", label: "Retriever", icon: "", x: 82, y: 50, category: "rag", description: "Fetches relevant documents" },
      { id: "context_builder", label: "Context Builder", icon: "", x: 82, y: 25, category: "rag", description: "Assembles context" },
      { id: "llm", label: "LLM Engine", icon: "", x: 68, y: 25, category: "rag", description: "Generates response" },
      { id: "knowledge_base", label: "Knowledge Base", icon: "", x: 52, y: 75, category: "data", description: "Document storage" },
      { id: "chunker", label: "Chunker", icon: "", x: 68, y: 75, category: "data", description: "Splits documents" },
      { id: "db", label: "Database", icon: "", x: 22, y: 75, category: "data", description: "Metadata storage" },
      { id: "cache", label: "Cache", icon: "", x: 38, y: 50, category: "infra", description: "Redis cache" },
      { id: "cloud", label: "Cloud", icon: "", x: 92, y: 75, category: "infra", description: "Infrastructure" },
    ],
    connections: [
      { from: "user", to: "frontend", label: "Query", type: "main" },
      { from: "frontend", to: "api", label: "REST", type: "main" },
      { from: "api", to: "query_processor", label: "Process", type: "main" },
      { from: "query_processor", to: "embeddings", label: "Encode", type: "rag" },
      { from: "embeddings", to: "vector_db", label: "Search", type: "rag" },
      { from: "vector_db", to: "retriever", label: "Top-K", type: "rag" },
      { from: "retriever", to: "context_builder", label: "Chunks", type: "rag" },
      { from: "context_builder", to: "llm", label: "Prompt", type: "rag" },
      { from: "llm", to: "query_processor", label: "Response", type: "rag" },
      { from: "knowledge_base", to: "chunker", label: "Ingest", type: "data" },
      { from: "chunker", to: "embeddings", label: "Embed", type: "data" },
      { from: "chunker", to: "vector_db", label: "Store", type: "data" },
      { from: "api", to: "cache", label: "Check", type: "infra" },
      { from: "cache", to: "embeddings", label: "Miss", type: "infra" },
      { from: "api", to: "db", label: "Metadata", type: "infra" },
      { from: "vector_db", to: "cloud", label: "Hosted", type: "infra" },
      { from: "llm", to: "cloud", label: "GPU", type: "infra" },
    ],
  };

  const getVisibleData = () => {
    if (viewMode === "full") return arch;
    if (viewMode === "rag") {
      return {
        nodes: arch.nodes.filter((n) => ["rag", "data"].includes(n.category)),
        connections: arch.connections.filter((c) => ["rag", "data"].includes(c.type)),
      };
    }
    if (viewMode === "simple") {
      return {
        nodes: arch.nodes.filter((n) => n.category === "main"),
        connections: arch.connections.filter((c) => c.type === "main"),
      };
    }
    return arch;
  };

  const visibleData = getVisibleData();

  const getCategoryColor = (category) => {
    const colors = { main: "#667eea", rag: "#f472b6", data: "#4ade80", infra: "#fbbf24" };
    return colors[category] || "#667eea";
  };

  const getGradientId = (type) => `${type}Gradient`;
  const getMarkerId = (type) => `arrow${type.charAt(0).toUpperCase() + type.slice(1)}`;
  const getNodeGradientId = (category) => `nodeGradient${category.charAt(0).toUpperCase() + category.slice(1)}`;

  // Check if node is currently active in demo
  const isNodeActive = (nodeId) => {
    if (!isPlaying && currentStep === -1) return false;
    const stepIndex = demoSteps.findIndex((s) => s.nodeId === nodeId);
    return stepIndex === currentStep;
  };

  // Check if node has been visited in demo
  const isNodeVisited = (nodeId) => {
    if (!isPlaying && currentStep === -1) return false;
    const stepIndex = demoSteps.findIndex((s) => s.nodeId === nodeId);
    return stepIndex < currentStep;
  };

  const currentDemoStep = currentStep >= 0 ? demoSteps[currentStep] : null;

  return (
    <div className="rag-architecture-diagram">
      {/* Header */}
      <div className="rag-diagram-header">
        <div className="rag-header-left">
          <h3> AI Search Architecture with RAG</h3>
          <div className="rag-legend">
            <span className="rag-legend-item"><span className="dot main"></span>Main</span>
            <span className="rag-legend-item"><span className="dot rag"></span>RAG</span>
            <span className="rag-legend-item"><span className="dot data"></span>Data</span>
            <span className="rag-legend-item"><span className="dot infra"></span>Infra</span>
          </div>
        </div>
        <div className="rag-header-controls">
          <div className="rag-view-toggle">
            {["simple", "rag", "full"].map((mode) => (
              <button
                key={mode}
                className={`rag-view-btn ${viewMode === mode ? "active" : ""}`}
                onClick={() => setViewMode(mode)}
                disabled={isPlaying}
              >
                {mode.charAt(0).toUpperCase() + mode.slice(1)}
              </button>
            ))}
          </div>
          
          {/* Demo Button */}
          {!isPlaying && currentStep === -1 ? (
            <button className="demo-play-btn" onClick={startDemo}>
               Play Demo
            </button>
          ) : (
            <button className="demo-reset-btn" onClick={resetDemo}>
               Reset
            </button>
          )}
        </div>
      </div>

      {/* Demo Query Input */}
      {!isPlaying && currentStep === -1 && (
        <div className="demo-query-input">
          <span className="query-label">Demo Query:</span>
          <input
            type="text"
            value={demoQuery}
            onChange={(e) => setDemoQuery(e.target.value)}
            placeholder="Enter a sample query..."
          />
        </div>
      )}

      {/* Demo Progress Bar */}
      {(isPlaying || currentStep >= 0) && (
        <div className="demo-progress">
          <div className="demo-progress-bar">
            <div 
              className="demo-progress-fill"
              style={{ width: `${((currentStep + 1) / demoSteps.length) * 100}%` }}
            />
          </div>
          <div className="demo-progress-steps">
            {demoSteps.map((step, i) => (
              <div
                key={i}
                className={`demo-step-dot ${i === currentStep ? "active" : ""} ${i < currentStep ? "done" : ""}`}
              />
            ))}
          </div>
        </div>
      )}

      {/* Live Output Panel */}
      {currentDemoStep && (
        <div className="demo-output-panel">
          <div className="demo-output-header">
            <span className="demo-step-badge">Step {currentStep + 1}/{demoSteps.length}</span>
            <span className="demo-step-title">{currentDemoStep.title}</span>
          </div>
          <div className="demo-output-content">
            <div className="demo-output-label">Output:</div>
            <code className="demo-output-code">{currentDemoStep.output}</code>
          </div>
          <div className="demo-output-detail">{currentDemoStep.detail}</div>
        </div>
      )}

      {/* Pipeline Steps */}
      <div className="rag-pipeline-steps">
        {["Query", "Embed", "Search", "Retrieve", "Context", "Generate"].map((step, i) => (
          <React.Fragment key={step}>
            <div className={`rag-pipeline-step ${currentStep >= i * 1.5 ? "active" : ""}`}>
              <span className="step-num">{i + 1}</span>
              <span className="step-label">{step}</span>
            </div>
            {i < 5 && <span className="step-arrow"></span>}
          </React.Fragment>
        ))}
      </div>

      {/* SVG Diagram */}
      <svg viewBox="0 0 100 90" className="rag-diagram-svg" preserveAspectRatio="xMidYMid meet">
        <defs>
          {/* Gradients */}
          <linearGradient id="mainGradient" x1="0%" y1="0%" x2="100%" y2="0%">
            <stop offset="0%" stopColor="#667eea" /><stop offset="100%" stopColor="#764ba2" />
          </linearGradient>
          <linearGradient id="ragGradient" x1="0%" y1="0%" x2="100%" y2="0%">
            <stop offset="0%" stopColor="#ec4899" /><stop offset="100%" stopColor="#f472b6" />
          </linearGradient>
          <linearGradient id="dataGradient" x1="0%" y1="0%" x2="100%" y2="0%">
            <stop offset="0%" stopColor="#22c55e" /><stop offset="100%" stopColor="#4ade80" />
          </linearGradient>
          <linearGradient id="infraGradient" x1="0%" y1="0%" x2="100%" y2="0%">
            <stop offset="0%" stopColor="#f59e0b" /><stop offset="100%" stopColor="#fbbf24" />
          </linearGradient>

          {/* Active Gradient (for demo) */}
          <radialGradient id="activeNodeGradient" cx="50%" cy="50%">
            <stop offset="0%" stopColor="#fef08a" />
            <stop offset="50%" stopColor="#facc15" />
            <stop offset="100%" stopColor="#eab308" />
          </radialGradient>

          {/* Node Gradients */}
          <radialGradient id="nodeGradientMain" cx="30%" cy="30%">
            <stop offset="0%" stopColor="#818cf8" /><stop offset="100%" stopColor="#667eea" />
          </radialGradient>
          <radialGradient id="nodeGradientRag" cx="30%" cy="30%">
            <stop offset="0%" stopColor="#f9a8d4" /><stop offset="100%" stopColor="#ec4899" />
          </radialGradient>
          <radialGradient id="nodeGradientData" cx="30%" cy="30%">
            <stop offset="0%" stopColor="#86efac" /><stop offset="100%" stopColor="#22c55e" />
          </radialGradient>
          <radialGradient id="nodeGradientInfra" cx="30%" cy="30%">
            <stop offset="0%" stopColor="#fcd34d" /><stop offset="100%" stopColor="#f59e0b" />
          </radialGradient>

          {/* Glow Filters */}
          <filter id="ragGlow">
            <feGaussianBlur stdDeviation="0.8" result="blur" />
            <feMerge><feMergeNode in="blur" /><feMergeNode in="SourceGraphic" /></feMerge>
          </filter>
          <filter id="activeGlow">
            <feGaussianBlur stdDeviation="2" result="blur" />
            <feMerge><feMergeNode in="blur" /><feMergeNode in="blur" /><feMergeNode in="SourceGraphic" /></feMerge>
          </filter>

          {/* Arrow Markers */}
          <marker id="arrowMain" markerWidth="10" markerHeight="7" refX="9" refY="3.5" orient="auto">
            <polygon points="0 0, 10 3.5, 0 7" fill="#667eea" />
          </marker>
          <marker id="arrowRag" markerWidth="10" markerHeight="7" refX="9" refY="3.5" orient="auto">
            <polygon points="0 0, 10 3.5, 0 7" fill="#f472b6" />
          </marker>
          <marker id="arrowData" markerWidth="10" markerHeight="7" refX="9" refY="3.5" orient="auto">
            <polygon points="0 0, 10 3.5, 0 7" fill="#4ade80" />
          </marker>
          <marker id="arrowInfra" markerWidth="10" markerHeight="7" refX="9" refY="3.5" orient="auto">
            <polygon points="0 0, 10 3.5, 0 7" fill="#fbbf24" />
          </marker>
        </defs>

        {/* Background Grid */}
        <pattern id="ragGrid" width="10" height="10" patternUnits="userSpaceOnUse">
          <path d="M 10 0 L 0 0 0 10" fill="none" stroke="rgba(255,255,255,0.03)" strokeWidth="0.3" />
        </pattern>
        <rect width="100" height="90" fill="url(#ragGrid)" />

        {/* Connections */}
        {visibleData.connections.map((conn, index) => {
          const fromNode = arch.nodes.find((n) => n.id === conn.from);
          const toNode = arch.nodes.find((n) => n.id === conn.to);
          if (!fromNode || !toNode) return null;

          const midX = (fromNode.x + toNode.x) / 2;
          const midY = (fromNode.y + toNode.y) / 2;

          // Check if this connection is part of the demo flow
          const fromStepIndex = demoSteps.findIndex((s) => s.nodeId === conn.from);
          const toStepIndex = demoSteps.findIndex((s) => s.nodeId === conn.to);
          const isActiveConnection = fromStepIndex === currentStep - 1 && toStepIndex === currentStep;

          return (
            <g key={`${conn.from}-${conn.to}`} className={`rag-connection ${isAnimating ? "animating" : ""} ${isActiveConnection ? "demo-active" : ""}`}>
              <line
                x1={fromNode.x} y1={fromNode.y}
                x2={toNode.x} y2={toNode.y}
                stroke={isActiveConnection ? "#facc15" : `url(#${getGradientId(conn.type)})`}
                strokeWidth={isActiveConnection ? "0.8" : "0.4"}
                opacity={isActiveConnection ? "1" : "0.6"}
                markerEnd={`url(#${getMarkerId(conn.type)})`}
              />
              {isAnimating && !isPlaying && (
                <circle r="0.7" fill={getCategoryColor(conn.type)} filter="url(#ragGlow)">
                  <animateMotion
                    dur={`${1.5 + (index % 5) * 0.3}s`}
                    repeatCount="indefinite"
                    path={`M${fromNode.x},${fromNode.y} L${toNode.x},${toNode.y}`}
                  />
                </circle>
              )}
              {/* Demo animated packet */}
              {isActiveConnection && (
                <circle r="1.2" fill="#facc15" filter="url(#activeGlow)">
                  <animateMotion
                    dur="0.8s"
                    repeatCount="1"
                    fill="freeze"
                    path={`M${fromNode.x},${fromNode.y} L${toNode.x},${toNode.y}`}
                  />
                </circle>
              )}
              <text x={midX} y={midY - 1.5} fontSize="1.5" fill="rgba(255,255,255,0.5)" textAnchor="middle">
                {conn.label}
              </text>
            </g>
          );
        })}

        {/* Nodes */}
        {visibleData.nodes.map((node) => {
          const isActive = isNodeActive(node.id);
          const isVisited = isNodeVisited(node.id);

          return (
            <g
              key={node.id}
              transform={`translate(${node.x}, ${node.y})`}
              onMouseEnter={() => !isPlaying && setActiveNode(node.id)}
              onMouseLeave={() => setActiveNode(null)}
              style={{ cursor: "pointer" }}
              className={`rag-node ${activeNode === node.id ? "hover" : ""} ${isActive ? "demo-active" : ""} ${isVisited ? "demo-visited" : ""}`}
            >
              {/* Active pulse for demo */}
              {isActive && (
                <>
                  <circle r="9" fill="none" stroke="#facc15" strokeWidth="0.5" opacity="0.8">
                    <animate attributeName="r" from="6" to="12" dur="0.8s" repeatCount="indefinite" />
                    <animate attributeName="opacity" from="0.8" to="0" dur="0.8s" repeatCount="indefinite" />
                  </circle>
                  <circle r="7" fill="none" stroke="#facc15" strokeWidth="0.3" opacity="0.5">
                    <animate attributeName="r" from="6" to="10" dur="0.6s" repeatCount="indefinite" />
                    <animate attributeName="opacity" from="0.5" to="0" dur="0.6s" repeatCount="indefinite" />
                  </circle>
                </>
              )}

              {/* Visited checkmark glow */}
              {isVisited && (
                <circle r="6" fill="rgba(74, 222, 128, 0.2)" />
              )}

              {/* Node circle */}
              <circle
                r="5"
                fill={isActive ? "url(#activeNodeGradient)" : `url(#${getNodeGradientId(node.category)})`}
                filter={isActive ? "url(#activeGlow)" : "url(#ragGlow)"}
                stroke={isVisited ? "#4ade80" : "none"}
                strokeWidth={isVisited ? "0.5" : "0"}
              />
              
              {/* Icon */}
              <text textAnchor="middle" dy="1.5" fontSize="3" fill="#fff">
                {isVisited && !isActive ? "" : node.icon}
              </text>
              
              {/* Label */}
              <text textAnchor="middle" dy="10" fontSize="2" fill={isActive ? "#facc15" : "rgba(255,255,255,0.9)"} fontWeight="500">
                {node.label}
              </text>
            </g>
          );
        })}
      </svg>

      {/* Hover Tooltip (only when not in demo) */}
      {activeNode && !isPlaying && currentStep === -1 && (
        <div className={`rag-tooltip tooltip-${arch.nodes.find((n) => n.id === activeNode)?.category}`}>
          <div className="rag-tooltip-header">
            <span className="rag-tooltip-icon">{arch.nodes.find((n) => n.id === activeNode)?.icon}</span>
            <div>
              <div className="rag-tooltip-title">{arch.nodes.find((n) => n.id === activeNode)?.label}</div>
              <span className={`rag-tooltip-badge ${arch.nodes.find((n) => n.id === activeNode)?.category}`}>
                {arch.nodes.find((n) => n.id === activeNode)?.category?.toUpperCase()}
              </span>
            </div>
          </div>
          <p className="rag-tooltip-desc">{arch.nodes.find((n) => n.id === activeNode)?.description}</p>
        </div>
      )}

      {/* Final Result Panel */}
      {currentStep === demoSteps.length - 1 && (
        <div className="demo-final-result">
          <div className="demo-final-header">
            <span></span> Query Complete!
          </div>
          <div className="demo-final-query">
            <strong>Q:</strong> {demoQuery}
          </div>
          <div className="demo-final-answer">
            <strong>A:</strong> Machine learning is a subset of artificial intelligence that enables systems to automatically learn and improve from experience without being explicitly programmed...
          </div>
          <div className="demo-final-stats">
            <span> 1.2s</span>
            <span> 3 sources</span>
            <span> 94% confidence</span>
          </div>
        </div>
      )}

      {/* Info Box (hidden during demo) */}
      {!isPlaying && currentStep === -1 && (
        <div className="rag-info-box">
          <div className="rag-info-title"> RAG Benefits</div>
          <ul>
            <li> Grounded responses</li>
            <li> Up-to-date knowledge</li>
            <li> Reduced hallucinations</li>
            <li> Domain-specific accuracy</li>
          </ul>
        </div>
      )}
    </div>
  );
};

export default memo(ArchitectureDiagramRAG);